import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Read from "../views/Read.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/read/:id",
    name: "Read",
    component: Read,
  },
  {
    path: "/:page?/:category?",
    name: "Home",
    component: Home,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
